.content-body {  
    &.pickup-content{  
        padding-bottom: 0;
        .subMenu-container {
            margin-top: 10px;
            &:after {
                @include clearBoth;
            }
            .select-box {
                float: right;
                position: relative;
                padding-right: 17px;
                &:after {
                    width: 10px;
                    height: 8px;
                    content: "";
                    position: absolute;
                    background: url(../img/icon_select01.png) center center no-repeat;
                    background-size: cover;
                    right: 0;           
                    top: 50%;                    
                    transform: translate(0,-50%);
                }
                .listUp-btn {
                    border: 0;
                    height: 18px;
                    @include fontSize(12px,500);
                }
            }
        }
        .pickupList-container {    
            margin-top: 5px;
            .none-list {
                @include fontSize(12px,500);
                color: #999;
                text-align: center;
                line-height: calc( 100vh - 87px );
            }
            .pickup-wrapper {
                padding: 20px 0;
                border-bottom: 1px solid #eeeeee;
                &:first-child {
                    border-top: 1px solid #eee;
                }
                .word-box {
                    .date {
                        margin-bottom: 15px;
                        position: relative;
                        p,span {
                            color: #333333;
                            line-height: 14px;
                            @include fontSize(14px,500);
                        }
                        button {
                            position: absolute;
                            @include btnNone;
                            height: 7px;
                            right: 0;
                            top: 50%;                    
                            transform: translate(0,-50%);
                            @include flexAllCenter;
                        }
                    }
                    .storeInfo {
                        margin-bottom: 4px;
                        span {
                            color: #fff;
                            @include fontSize(12px,300);
                            line-height: 24px;
                            height: 23px;
                            background: #000;
                            padding: 0 9px;
                            border-radius: 12px;
                            margin-right: 5px;
                            display: inline-block;
                        }
                    }
                    .goods-name {
                        margin-bottom: 15px;
                        p {
                            @include fontSize(14px,500);
                            line-height: 20px;
                            @include wordClear;
                        }
                    }
                    .price {
                        &:after {
                            @include clearBoth;
                        }                   
                        .goods-status {
                            float: left;
                            @include flexCenter;
                            .select-status {
                                color: $mainColor;
                                @include fontSize(12px,500);
                                line-height: 18px;
                            }
                            button {
                                @include btnNone;
                                @include fontSize(11px,100);
                                @include flexCenter;
                                border-bottom: .5px solid rgba(#999,.2);
                                color: #999;
                                margin-left: 7px;
                                img {
                                    width: 4px;
                                    margin-left: 3px;
                                }
                            }
                        }
                        .priceNum {
                            float: right;
                            color: #333;
                            @include fontSize(12px,400);
                            span {
                                color: #333;
                                @include fontSize(15px,700);
                                line-height: 18px;
                            }
                        }
                    }
                }
            }
        }
    }
}

