.content-body {
    &.nonmemberLookup {
        .user-container {
            padding-top: 10px;
            height: calc( 100vh - 114px );
            position: relative;
            .input-wrap {
                width: 100%;
                margin-bottom: 25px;
                .input-title {
                    @include fontSize(14px, 500);
                    color: #000;
                    margin-bottom: 5px;                        
                }
                .input-content {
                    width: 100%;
                    &.has-btn {
                        @include flexCenter;
                        .input-box {
                            width: calc( 100% - 100px );
                            margin-right: 10px;
                        }
                    }
                    .input-box {                   
                        input {
                            width: 100%;
                            padding: 0 10px;
                            height: 35px;
                            border: none;
                            border-bottom: 1px solid rgba(#000, .2);
                            @include fontSize(15px,500);
                        }
                    }
                }
                button {
                    @include fontSize(14px,400);
                    line-height: 23px;
                    color: #fff;
                    background: #666;
                    border: 0;
                    height: 35px;
                    width: 90px;                 
                }
            }
            .btn-wrap {
                position: absolute;
                width: 100%;
                bottom: 30px;
                button {
                    border: 1px solid rgba(#000, .1);
                    background: 0;
                    @include fontSize(14px,400);
                    line-height: 23px;
                    color: rgba(#000, .4);
                    height: 40px;                  
                    width: 100%;
                }
            }
        }
        .btn-container {
            @include flexCenter;
            position: fixed;
            bottom: 0;
            width: 100%;
            button {
                border: 0;
                height: 60px;
                width: 50%;
                color: #fff;
                @include fontSize(15px,500);                       
                line-height: 22px;
                background: #666;
                &.okBtn {
                    background: $mainColor;
                }
            }
        }
    }
}