.content-body {
    &.write-content {
        padding-top: 64px;
        .goodsHeader-container {
            margin-bottom: 20px;
            @include flexCenter;
            .img-box {
                width: 50px;
                height: 50px;
                background: url(../img/image_01.png) center center;
                background-size: cover;
                margin-right: 10px;
            }
            .word-box {
                height: 50px;
                width: calc( 100% - 60px );
                .goods-subject {
                    @include fontSize(12px,700);
                    line-height: 18px;
                    @include wordClear;
                    width: 100%;
                    margin-bottom: 10px;
                }
                .goods-price {
                    p {
                        text-align: right;
                        @include fontSize(14px,500);
                        color: #333;
                        span {
                            @include fontSize(18px,700);
                            color: #333;
                        }
                    }
                }
            }
        }
        .goodsSelect-container {
            margin-bottom: 15px;
            border-bottom: 1px solid #eee;
            position: relative;        
            select {
                width: 100%;
                border: 0;
                @include fontSize(15px,500);
                line-height: 23px;
                height: 35px;
                padding: 0 10px;
                &.noneVal {
                    color: #c4c4c4;
                    @include fontSize(14px,300);
                    line-height: 23px;
                }
                option {
                    color: #000;
                }
            }
            .fake-arrow {
                position: absolute;
                right: 10px;
                top: 50%;
                transform: translate(0,-50%);
                width: 10px;
            }
        }
        .goodsWord-container {
            &.starArea {
                .upload-btn {
                    label {
                        width: 80px;
                        height: 80px;
                        background: url(../img/food.png) center center;
                        background-size: cover;
                    }

                }
            }
            &:after {
                @include clearBoth;
            }
            ::placeholder {
                @include fontSize(12px,300);
                color: #c4c4c4;
                line-height: 23px;
            }
            .upload-btn {
                margin-top: 10px;
                position: relative;
                width: 80px;
                height: 80px;
                label {
                    width: 80px;
                    height: 80px;
                    background: url(../img/icon_photoup.png) center center;
                    background-size: cover;
                    .delete-btn {
                        right: 0;
                        top: 0;
                        @include btnNone;
                        position: absolute;
                        @include flexAllCenter;
                        img {
                            width: 30px;
                        }
                    }
                }
                input[type="file"] {
                    opacity: 0;
                    position: absolute;
                    top: 0;
                    left: 0;
                    bottom: 0;
                    width: 100%;
                }
            }
            textarea {
                resize: none;
                width: 100%;
                border: 1px solid #eee;
                height: calc( 100vh - 308px );
                padding: 10px;
                @include fontSize(12px,400);
                color: #333;
                line-height: 23px;
                &.noneCheck {
                    height: calc( 100vh - 205px );
                }
                &.hasPhoto {
                    height: calc( 100vh - 353px );
                }
                &:focus {
                    border: 1px solid #000;
                }
            }
            input[type="checkbox"] {
                display:none;
            }
            input[type="checkbox"] + label .checkbox-custom {
                display:inline-block;
                width: 15px;
                height: 15px;
                vertical-align:middle;
                cursor:pointer;
                border: 1px solid #c9c9c9;
                background: #fff;
                -webkit-background-size: 100%; background-size: 100%;
            }
            input[type="checkbox"]:checked + label .checkbox-custom {
                width: 15px;
                height: 15px;
                border: none;
                background: url(../img/icon_checkbox_on.png) center center no-repeat;
                -webkit-background-size: 100%; background-size: 100%;
            }
            .checkbox-label {
                margin-left: 5px;
                @include fontSize(12px,500);
                line-height: 18px;
            }
            .checkbox-wrap {
                margin-top: 15px;
                float: right;
                >label {
                    @include flexCenter;
                }
            }
        }
        .goodsScore-container {
            &:after {
                @include clearBoth;
            }
            &.starArea {                
                .starScore {                
                    color: #fed220;
                    @include fontSize(24px,700);
                    line-height: 29px;
                    margin-right: 10px;           
                }
            }    
            margin-bottom: 15px;
            .star-input {
                float: right;
                @include flexCenter;
                height: 29px;
                output {
                    margin-right: 10px;
                }
                .input {
                    outline: none;
                }
            }
        }
    }
}