.content-body {
    &.orderDetail {
        padding: 54px 0 60px;
        position: relative;
        .status-text {
            position: absolute;
            right: 15px;
            top: 64px;
            @include fontSize(10px,400);
            background: rgba(#ee5322,.2);
            color: #ee5322;
            line-height: 19px;
            height: 19px;
            padding: 0 6px;
        }
        .pickup-header {
            padding: 10px 15px 0;
            .pickup-number {
                @include fontSize(12px,900);
                span {
                    @include fontSize(20px,900);
                    margin-left: 2px;
                }
            }
        }
        .goods-container {
            padding: 10px 15px 5px;
            .container-title {
                @include fontSize(14px,700);
                line-height: 23px;
                margin-bottom: 10px;
            }            
            .store-container {
                &.orderCancel {
                    .store-wrap {
                        .container-content {
                            @include flexCenter;
                            &.cancel-goods {
                                pointer-events: none;
                                .menu-area,.bottom-area {
                                    opacity: .5;
                                }
                            }
                            .checkbox-wrap {
                                margin-right: 10px;
                                input[type="checkbox"] {
                                    display:none;
                                }
                                input[type="checkbox"] + label .checkbox-custom {
                                    display:inline-block;
                                    width: 15px;
                                    height: 15px;
                                    vertical-align:middle;
                                    cursor:pointer;
                                    border: 1px solid #c9c9c9;
                                    background: #fff;
                                    -webkit-background-size: 100%; background-size: 100%;
                                }
                                input[type="checkbox"]:checked + label .checkbox-custom {
                                    width: 15px;
                                    height: 15px;
                                    border: none;
                                    background: url(../img/icon_checkbox_on.png) center center no-repeat;
                                    -webkit-background-size: 100%; background-size: 100%;
                                }
                            }
                            .word-wrapper {
                                width: calc( 100% - 115px );
                                &.none-check {
                                    width: calc( 100% - 90px );
                                }
                                .menu-area {
                                    p {
                                        width: 100%;
                                        @include wordClear;
                                    }
                                }
                                .bottom-area {
                                    position: relative;
                                    .numCount {
                                        position: absolute;
                                        left: 0;
                                        bottom: 0;
                                        >button {
                                            float: left;
                                            background: none;
                                            border: 1px solid #eee;
                                            width: 30px;
                                            height: 28px;
                                            &.maximum {
                                                img {
                                                    opacity: .3;
                                                }
                                            }
                                        }
                                        .goodsAmount {
                                            float: left;
                                            width: 47px;
                                            height: 28px;
                                            border: 1px solid #eee;
                                            @include flexAllCenter;
                                            span {
                                                color: #333;
                                                @include fontSize(12px,400);
                                                line-height: 14px;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
                .store-wrap {
                    padding-top: 20px;
                    border-bottom: 1px solid #eee;
                    &:first-child {
                        padding-top: 0;
                    }
                    &:last-child {
                        border-bottom: 0;
                    }
                    .store-title {
                        margin-bottom: 10px;
                        @include flexCenter;
                        span {
                            padding: 0 9px;
                            height: 23px;
                            line-height: 24px;
                            background: #000;
                            border-radius: 99px;
                            color: #fff;
                            @include fontSize(12px,300);
                            margin-right: 5px;
                        }
                        .address {
                            @include fontSize(12px,400);
                            color: #333;
                        }
                    }
                    .container-content {
                        padding-bottom: 15px;
                        &:after {
                            @include clearBoth;
                        }
                        &:last-child {
                            padding-bottom: 20px;
                        }
                        .auto-cancel {
                            text-align: center;
                            padding-top: 15px;
                            float: left;
                            width: 100%;
                            p {
                                @include fontSize(14px,400);
                                span {
                                    @include fontSize(14px,700);
                                    color: $mainColor;
                                }
                            }
                        }
                        .img-wrapper {
                            background: url(../img/image_09.png) center center;
                            width: 80px;
                            height: 80px;
                            margin-right: 10px;
                            float: left;    
                        }
                        .word-wrapper {
                            width: calc( 100% - 90px);
                            float: right;
                            height: 80px;
                            position: relative;
                            .status-area {
                                margin-bottom: 5px;
                                @include flexCenter;
                                >span {
                                    @include fontSize(10px,500);
                                    width: 34px;
                                    height: 19px;
                                    display: inline-block;
                                    text-align: center;
                                    line-height: 18px;
                                    &.blue-box {
                                        border: 1px solid rgba(#2962e6,.5);
                                        color: #2962e6;
                                    }
                                    &.green-box {
                                        border: 1px solid rgba(#14a4b5,.5);
                                        color: #14a4b5;
                                    }
                                }
                                .cancel {
                                    @include fontSize(12px,500);
                                    line-height: 19px;
                                    color: $redColor;
                                    margin-left: 5px;
                                }
                                .clear-text {
                                    margin-left: 5px;
                                }
                                .clear-text,.clear-text span {
                                    @include fontSize(12px,400);
                                    color: rgba($mainColor,.5);
                                }
                            }
                            .menu-area {
                                margin-bottom: 12px;
                                p {
                                    color: #333;
                                    @include fontSize(14px,500);
                                    line-height: 20px;
                                    @include flexCenter;
                                    display: -webkit-box;
                                    -webkit-box-orient: vertical;
                                    -webkit-line-clamp: 2;
                                    overflow: hidden;
                                }
                            }
                            .bottom-area {
                                position: absolute;
                                right: 0;
                                bottom: -2px;
                                &:after {
                                    @include clearBoth;
                                }
                                &:after {
                                    @include clearBoth;
                                }
                                .price-box { 
                                    float: right;
                                    &:after {
                                        @include clearBoth;
                                    }
                                    .priceNum {
                                        float: left;
                                        color: #333;
                                        @include fontSize(12px,400);
                                        margin-right: 5px;
                                        line-height: 18px;
                                        span {
                                            color: #333;
                                            @include fontSize(15px,700);
                                            line-height: 18px;
                                        }
                                    }
                                    .pricePer {
                                        float: right;
                                        color: #333;
                                        @include fontSize(12px,400);
                                        line-height: 18px;
                                        span {
                                            color: #333;
                                            @include fontSize(15px,700);
                                            line-height: 18px;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
        .accordion-container {
            padding: 25px 15px;
            width: 100%;
            &.open-action {
                transition: .5s;
                padding-bottom: 15px;
                >.container-title {
                    img {
                        transform: translate(0,-50%) rotate(180deg);
                    }
                }
            }
            &.close-action {
                transition: .5s;
                padding: 25px 15px;
                >.container-title {
                    img {
                        transform: translate(0,-50%) rotate(0);
                    }
                }
            }
            .container-title {
                position: relative;
                p {
                    @include fontSize(14px,700);
                    line-height: 20px;
                }
                img {
                    position: absolute;
                    right: 0;
                    top: 50%;
                    transform: translate(0,-50%);
                    width: 13px;
                }
            }
            .container-list {
                .list-wrapper {
                    padding: 20px 0;
                    border-bottom: 1px solid #eee;
                    &:first-child {
                        padding-top: 0;
                    }
                    &:last-child {
                        border-bottom: 0;
                        padding-bottom: 15px;
                    }
                    >ul {
                        padding: 10px 0;
                        border-bottom: 1px solid #eee;
                        &:last-child {
                            padding-bottom: 0;
                            border-bottom: 0;
                            &.focus {
                                padding-top: 20px;
                                .focus-content {
                                    color: $mainColor;
                                    span {
                                        color: $mainColor;
                                        @include fontSize(23px,700);
                                        margin-right: 1px;
                                    }
                                }
                            }
                        }
                        &.custom-border {
                            border-bottom: 0;
                        }
                        >li {
                            margin-bottom: 5px;
                            &:last-child {
                                margin-bottom: 0;
                            }
                            &.custom-margin {
                                margin-bottom: 12px;
                            }
                            ul {
                                position: relative;
                                li {
                                    &.list-title {
                                        @include fontSize(14px,400);
                                        color: #333;
                                        line-height: 20px;
                                    }
                                    &.list-content {
                                        right: 0;
                                        bottom: 0;
                                        position: absolute;
                                        @include fontSize(14px,500);
                                        span {
                                            @include fontSize(14px,500);
                                        }
                                    }
                                }
                            }
                        }
                    }
                    .alert-text {
                        text-align: center;
                        margin-top: 15px;
                        p {
                            @include fontSize(14px,400);
                            span {
                                @include fontSize(14px,400);
                                text-decoration-line: underline;
                            }
                            strong {
                                @include fontSize(14px,700);
                                color: $mainColor;
                            }
                        }
                    }
                }
            }
        }
        .btn-area {
            .okBtn {
                border: 0;
                height: 60px;
                width: 100%;
                background: $mainColor;
                color: #fff;
                @include fontSize(15px,500);
            }
        }
    }
}