.content-body {
    &.order-clear {
        .center-box {
            text-align: center;
            padding-top: 22vh;
            img {
                width: 116px;
                margin-bottom: 5px;
            }
            p {
                @include fontSize(15px,400);
                margin-bottom: 5px;
                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
    }
}