.content-body {  
    &.attention-content {
        .storeList-container {
            padding-top: 10px;
            .none-list {
                @include fontSize(12px,500);
                color: #999;
                text-align: center;
                line-height: calc( 100vh - 64px );
            }
            .list-wrapper {
                padding: 20px 0;
                border-bottom: 1px solid #eee;
                position: relative;
                &:first-child {
                    border-top: 1px solid #eee;
                }
                .delete-list {
                    @include flexAllCenter;
                    @include btnNone;
                    width: 40px;
                    height: 40px;
                    position: absolute;
                    right: -15px;
                    top: 5px;
                    z-index: 2;
                    img {
                        width: 10px;
                    }
                }
                .word-box {
                    .store-category {      
                        margin-bottom: 5px; 
                        position: relative;                    
                        &:after {
                            @include clearBoth;
                        }
                        span {
                            padding: 0 9px;
                            height: 23px;
                            line-height: 24px;
                            color: #fff;
                            @include fontSize(12px,300);
                            background: #000;
                            padding: 0 9px;
                            border-radius: 12px;
                            margin-right: 5px;
                            float: left;
                        }
                        button {
                            border: 1px solid $mainColor;
                            background: #fff;
                            position: absolute;
                            right: 0;
                            height: 28px;
                            width: 60px;
                            @include flexAllCenter;
                            p {
                                color: $mainColor;
                            }
                        }
                    }
                    .store-address {
                        p {
                            color: #333;
                            @include fontSize(14px,400);
                            line-height: 22px;
                        }
                    }
                    >small {
                        @include fontSize(12px,400);
                        color: #999;
                        line-height: 14px;
                        margin-top: 5px;
                        display: block;
                    }
                }
            }
        }
    }
}