.content-body {  
    &.goodsQA-content {
        padding: 54px 0px 0px;
        .target-wrap {
            .tabTarget {                           
                display: none;
                &.active {
                    @include flexAllCenter;
                } 
                .goodsData-container {
                    width: 100%;
                    height: calc( 100vh - 97px );
                    overflow: scroll;
                    padding-top: 5px;
                    .none-list {
                        @include fontSize(12px,500);
                        color: #999;
                        text-align: center;
                        line-height: calc( 100vh - 102px );
                    }
                    .list-item {
                        padding: 15px 15px 0;
                        border-bottom: 1px solid #eee;
                        &.questionOpen {
                            transition: .3s;
                            background: #f5f5f5;
                        }
                        .custom-bar {
                            margin-bottom: 7px;
                            &:after {
                                @include clearBoth;
                            }
                            &.complete {
                                p {
                                    span {
                                        @include fontSize(10px,400);
                                        line-height: 15px;
                                        color: #fff;
                                        padding: 2px 6px;
                                        background: $mainColor;
                                        display: inline-block;
                                    }
                                }
                            }
                            p {
                                float: left;                            
                                span {
                                    @include fontSize(10px,400);
                                    line-height: 15px;
                                    color: $mainColor;
                                    padding: 2px 6px;
                                    background: rgba($mainColor, .2);
                                    display: inline-block;
                                }
                            }
                            ul {
                                float: right;
                                @include flexCenter;
                                li {
                                    @include fontSize(11px,300);
                                    color: $mainColor;
                                    margin-right: 10px;
                                    line-height: 19px;
                                    &:last-child {
                                        margin-right: 0;
                                    }
                                }
                            }
                        }
                        .user-section {
                            padding-bottom: 15px;
                            .info-bar {
                                margin-bottom: 5px;
                                &:after {
                                    @include clearBoth;
                                }
                                .user-info {
                                    float: left;
                                    p {
                                        .store {
                                            color: #fff;
                                            @include fontSize(12px,300);
                                            line-height: 24px;
                                            height: 23px;
                                            background: #000;
                                            padding: 0 9px;
                                            border-radius: 12px;
                                            margin-right: 5px;
                                            display: inline-block;
                                        }
                                        .questionKinds {
                                            @include fontSize(12px,500);
                                            line-height: 18px;
                                            color: #999;
                                            margin-right: 5px;
                                        }
                                        .date {
                                            @include fontSize(12px,400);
                                            line-height: 14px;
                                            color: #333;
                                        }
                                    }
                                }
                                .more-btn {
                                    float: right;
                                    @include btnNone;
                                    @include flexAllCenter;
                                    height: 24px;
                                    &.active {
                                        img {
                                            transform: rotate(180deg);
                                        }
                                    }
                                    img {
                                        width: 13px;
                                    }
                                }
                            }
                            .title-bar {
                                @include fontSize(15px,500);
                                line-height: 22px;
                                color: #333;
                                margin-bottom: 5px;
                                @include wordClear;
                                width: 100%;
                                &.showing {
                                    text-overflow: initial;
                                    white-space: initial;
                                    overflow: initial;
                                }
                            }
                            .content-bar {
                                pre {
                                    @include fontSize(12px,400);
                                    line-height: 20px;
                                    color: #333;
                                }
                                .contentTitle {
                                    display: block;
                                    margin-bottom: 15px;
                                    @include fontSize(12px,400);
                                    line-height: 20px;
                                    color: #333;
                                }
                                .contenttxt {
                                    @include fontSize(12px,500);
                                    line-height: 20px;
                                    color: #333;
                                }
                            }
                        }
                        .manager-section {
                            padding-bottom: 15px;
                            display: none;
                            .answer {
                                color: $mainColor;
                                @include fontSize(12px,500);
                                line-height: 18px;
                                margin-bottom: 5px;
                            } 
                            .contenttxt {
                                @include fontSize(12px,500);
                                line-height: 20px;
                                color: #333;
                            }
                        }
                    }
                }
                .withOutQa {
                    @include fontSize(12px,500);
                    line-height: 18px;
                    color: #999;
                    text-align: center;             
                }
            }
        }
    }
}