.content-body {
    &.coupon-content {
        padding-bottom: 0;
        .content-wrapper {
            margin-top: 10px;
            .title-section {
                margin-bottom: 20px;
                .word-box {
                    margin-bottom: 15px;
                    @include flexCenter;
                    strong {
                        @include fontSize(12px,400);
                        color: #000;
                        line-height: 18px;
                        @include flexCenter;
                        span {
                            @include fontSize(14px,700);
                            line-height: 17px;
                            margin-left: 3px;
                            &.colorNum {
                                color: $mainColor;
                            }
                        }
                    }
                }
                .btn-box {
                    &:after {
                        @include clearBoth;
                    }
                    .order-item {
                        float: left;
                        width: calc( 100% - 100px );
                        input {
                            width: 100%;
                            padding: 0 10px;
                            height: 35px;
                            border: none;
                            line-height: 22px;
                            border-bottom: 1px solid rgba(#000, .2);
                            @include fontSize(15px,400); 
                            color: #000;
                        }
                        ::placeholder {
                            color: #c4c4c4;
                            @include fontSize(14px,300);                                    
                        }
                    }
                    button {
                        float: right;
                        border: none;
                        background: #666;
                        color: #fff;
                        line-height: 20px;
                        @include fontSize(14px,400);
                        width: 90px;
                        height: 35px;
                        &.active {
                            background: #000;
                        }
                    }
                }
            }
            .coupon-container {
                .none-list {
                    @include fontSize(12px,500);
                    color: #999;
                    text-align: center;
                    line-height: calc( 100vh - 152px );
                }
                .coupon-section {
                    margin-bottom: 15px;
                    &:after {
                        @include clearBoth;
                    }
                    .sale-box {
                        float: left;
                        width: calc( 100% - 100px );
                        border: 1px solid #eee;
                        height: 118px;
                        .word-item {
                            padding: 20px;
                            .toTop {
                                margin-bottom: 5px;
                                position: relative;
                                &:after {
                                    @include clearBoth;
                                }
                                .storeName {
                                    float: left;
                                    span {
                                        @include fontSize(12px,300);
                                        color: #fff;
                                        padding: 0 9px;
                                        line-height: 24px;
                                        height: 23px;
                                        background: #000;
                                        border-radius: 12px;
                                        display: block;
                                    }
                                }
                                .couponTerm {
                                    float: right;
                                    @include fontSize(10px,300);
                                    line-height: 13px;
                                    color: #333;
                                    position: absolute;
                                    right: 0;
                                    top: 50%;
                                    transform: translate(0,-50%);
                                }
                            }
                            .mainTxt {
                                @include fontSize(23px,500);
                                color: #000;
                                line-height: 34px;
                                span {
                                    @include fontSize(30px,700);
                                    color: #000;
                                    line-height: 37px;
                                }
                            }
                            .subTxt {
                                @include fontSize(10px,300);
                                color: #333;
                                line-height: 15px;
                            }
                        }
                    }
                    .dDay-box {
                        float: right;
                        width: 100px;
                        height: 118px;
                        background: #000;
                        @include flexAllCenter;
                        .dDay-item {
                            .countNumber {
                                @include fontSize(30px,700);
                                color: #fff;
                                line-height: 37px;
                                margin-bottom: 10px;
                            }
                            .countDate {
                                @include fontSize(10px,300);
                                color: #fff;
                                line-height: 15px;    
                                text-align: center;
                            }
                        }
                    }
                }
            }
        }        
    }
}