.content-body {  
    &.wish-content{
        padding-bottom: 0;
        .subMenu-container {
            padding-top: 10px;  
            .sub-txt {
                @include fontSize(12px,400);
                line-height: 18px;
                color: #666;
                margin-bottom: 20px;
            }
            .total-info {
                position: relative;
                margin-bottom: 5px;
                &:after {
                    @include clearBoth;
                }
                p {
                    @include fontSize(14px,400);
                    color: #333;
                    line-height: 20px;
                    float: left;
                    @include flexCenter;
                    strong {
                        @include fontSize(12px,400);
                        @include flexCenter;
                        margin-left: 3px;
                        span {
                            @include fontSize(14px,700);
                            &.colorNum {
                                color: $mainColor;
                            }
                        }
                    }
                }
                .clear {
                    color: #333;
                    @include fontSize(11px,300);
                    line-height: 17px;
                    background: none;
                    border: 0;
                    right: 0;
                    height: 17px;
                    position: absolute;
                    top: 50%;                    
                    transform: translate(0,-50%);                    
                }
            }
        }
        .wishList-container {
            .none-list {
                @include fontSize(12px,500);
                color: #999;
                text-align: center;
                line-height: calc( 100vh - 146px );
            }
            .wish-wrapper {
                @include flexCenter;
                padding: 20px 0;
                border-bottom: 1px solid #eeeeee;
                position: relative;
                &:first-child {
                    border-top: 1px solid #eee;
                }
                >button {
                    border: 0;
                    background: none;
                    position: absolute;
                    right: 0;
                    top: 20px;
                    height: 10px;
                    width: 10px;
                    @include flexAllCenter;
                    z-index: 1;
                    img {
                        width: 10px;
                    }
                }
                .img-box {
                    background: url(../img/image_09.png) center center;
                    width: 80px;
                    height: 80px;
                    margin-right: 10px;
                }
                .word-box {
                    height: 80px;
                    width: calc( 100% - 90px);
                    position: relative;   
                    &.cancle {
                        opacity: 0.5;
                    }
                    .toTop {      
                        margin-bottom: 7px; 
                        position: relative;                    
                        &:after {
                            @include clearBoth;
                        }
                        p {
                            float: left;
                            span {
                                display: inline-block;
                                color: #fff;
                                @include fontSize(12px,300);
                                line-height: 24px;
                                height: 23px;
                                background: #000;
                                padding: 0 9px;
                                border-radius: 12px;
                                margin-right: 5px;
                            }
                        }
                    }
                    .menuInfo {
                        @include fontSize(14px,500);
                        color: #333;
                        line-height: 20px;
                        p {
                            width: calc( 100% - 5px );
                            @include wordClear;
                        }
                    }
                    .price { 
                        position: absolute;
                        width: 100%;
                        bottom: -2px;
                        .priceNum {
                            float: right;
                            color: #333;
                            @include fontSize(12px,400);
                            line-height: 18px;
                            span {
                                color: #333;
                                @include fontSize(15px,700);
                                line-height: 18px;
                            }
                        }
                    }
                }
            }
        }
    }
}

