.content-body {
    &.photo-content {
        padding-bottom: 0;
        .review-container {
            &:after {
                @include clearBoth;
            }
            .review-item {
                width: calc((100% - 20px)/3);
                background: url(../img/image_09.png) center center;
                background-size: cover;
                height: 100px;
                float: left;
                margin-right: 10px;
                margin-bottom: 10px;
                &:nth-child(3n) {
                    margin-right: 0;
                }
            }
        }
    }
}