.content-body {
    &.ppGoods-content {
        padding-bottom: 0;
        .goodsList-container {
            .none-list {
                @include fontSize(12px,500);
                color: #999;
                text-align: center;
                line-height: calc( 100vh - 102px );
            }
            .goods-wrap {
                border-bottom: 1px solid #eee;
                margin-bottom: 20px;
                &:last-of-type {
                    margin-bottom: 0;
                }
                .goods-header {
                    margin-bottom: 15px;
                    &:after {
                        @include clearBoth;
                    }
                    .pickUp-num {
                        @include fontSize(12px,700);
                        float: left;
                        line-height: 18px;
                        span {
                            @include fontSize(20px,700);
                        }
                    }
                    .counting {
                        float: right;
                        color: $mainColor;
                        @include fontSize(12px,500);
                        line-height: 18px;
                        span {
                            color: $mainColor;
                            @include fontSize(12px,500);
                            margin-left: 3px;
                        }
                    }
                }
                .goods-item {
                    @include flexCenter;
                    padding-bottom: 10px;
                    &:last-of-type {
                        padding-bottom: 20px;
                    }
                    .img-box {
                        background: url(../img/image_09.png) center center;
                        background-size: cover;
                        width: 80px;
                        height: 80px;
                        margin-right: 10px;
                    }
                    .word-box {
                        height: 80px;
                        width: calc( 100% - 90px );
                        position: relative;
                        .box-header {
                            position: relative;
                            margin-bottom: 5px;
                            .store-name {
                                display: inline-block;
                                color: #fff;
                                @include fontSize(12px,300);
                                line-height: 24px;
                                height: 23px;
                                background: #000;
                                padding: 0 9px;
                                border-radius: 99px;
                            }
                            .option-box {
                                position: absolute;
                                right: 0;
                                top: 50%;
                                transform: translate(0,-50%);
                                @include fontSize(10px,500);
                                width: 34px;
                                height: 19px;
                                display: inline-block;
                                text-align: center;
                                line-height: 18px;
                                &.blue-box {
                                    border: 1px solid rgba(#2962e6,.5);
                                    color: #2962e6;
                                }
                                &.green-box {
                                    border: 1px solid rgba(#14a4b5,.5);
                                    color: #14a4b5;
                                }
                            }
                        }
                        .goodsSub {
                            p {
                                @include fontSize(14px,500);
                                color: #333;
                                line-height: 20px;
                                @include flexCenter;
                                @include wordClear;
                                width: 100%;
                            }
                        }
                        .toBottom {
                            position: absolute;
                            width: 100%;
                            bottom: 0;
                            &:after {
                                @include clearBoth;
                            }
                            .pickUp-status {
                                float: left;
                                @include fontSize(12px,500);
                                color: $mainColor;
                                line-height: 18px;
                            }
                            .priceNum {
                                float: right;
                                @include fontSize(12px,400);
                                color: #333;
                                span {
                                    @include fontSize(15px,700);
                                }
                                small {
                                    @include fontSize(15px,400);
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}