.content-body {
    &.reward {
        padding-bottom: 0;
        .card-container {
            width: 100%;
            height: 190px;
            background: #000;
            margin-top: 10px;
            margin-bottom: 20px;
            padding: 30px;
            .main-box {
                margin-bottom: 20px;
                .nickName {
                    color: #fff;
                    @include fontSize(18px,300);
                    line-height: 27px;
                    margin-bottom: 1px;
                    span {
                        color: #fff;
                        @include fontSize(18px,500);
                        line-height: 27px;
                        margin-right: 5px;
                    }
                }
                .totalPrice {
                    color: #fff;
                    @include fontSize(12px,500);
                    line-height: 14px;
                    span {
                        color: #fff;
                        @include fontSize(35px,700);
                        line-height: 42px;
                        margin-right: 5px;
                    }
                }
            }
            .sub-box {
                .sub-content {
                    margin-bottom: 5px;
                    &:after {
                        @include clearBoth;
                    }
                    .reward-name {
                        @include fontSize(12px,300);
                        line-height: 18px;
                        color: #fff;
                        float: left;
                    }
                    .reward-price {
                        @include fontSize(12px,700);
                        line-height: 14px;
                        color: #fff;
                        float: right;
                    }
                }
            }
        }
        .content-section {
            .none-list {
                @include fontSize(12px,500);
                color: #999;
                text-align: center;
                line-height: calc( 100vh - 315px );
            }
            .listTitle-box {
                padding-bottom: 5px;
                margin-bottom: 15px;
                border-bottom: 1px solid #eee;
                p {
                    @include fontSize(14px,700);
                    line-height: 20px;
                }
            }
            .list-box {
                margin-bottom: 15px;
                &:last-child {
                    margin-bottom: 0;
                }
                &.useCoin {
                    .rewardCoin{
                        color: $mainColor;
                        span {
                            color: $mainColor;
                        }
                    }
                }
                &:after {
                    @include clearBoth;
                }
                .rewardDate {
                    float: left;
                    color: #333;
                    @include fontSize(14px,400);
                    line-height: 17px;
                }
                .rewardCoin {
                    float: right;
                    @include fontSize(12px,400);
                    line-height: 18px;
                    span {
                        line-height: 18px;
                        @include fontSize(15px,700);
                        margin-right: 2px;
                    }
                }
            }
        }
    }
}