.content-body {  
    &.review-content {
        padding: 54px 0px 0;
        .target-wrap{          
            padding: 0 15px;
            height: calc( 100vh - 97px );
            overflow: scroll;
            .tabTarget {
                .none-list {
                    @include fontSize(12px,500);
                    color: #999;
                    text-align: center;
                    line-height: calc( 100vh - 97px );
                }
                .review-wrapper {
                    @include flexCenter;
                    padding: 20px 0;
                    border-bottom: 1px solid #eee;    
                    .img-box {
                        background: url(../img/image_09.png) center center;
                        width: 80px;
                        height: 80px;
                        margin-right: 10px;
                    }
                    .word-box {
                        height: 80px;
                        width: calc( 100% - 90px);
                        position: relative;   
                        &.cancle {
                            opacity: 0.5;
                        }
                        .toTop {      
                            margin-bottom: 7px; 
                            position: relative;                    
                            &:after {
                                @include clearBoth;
                            }
                            p {
                                float: left;
                                span {
                                    display: inline-block;
                                    color: #fff;
                                    @include fontSize(12px,300);
                                    line-height: 18px;
                                    background: #000;
                                    padding: 3px 9px;
                                    border-radius: 12px;
                                    margin-right: 5px;
                                }
                            }
                            button {
                                border: 1px solid $mainColor;
                                background: #fff;
                                position: absolute;
                                right: 0;
                                height: 28px;
                                width: 60px;
                                @include flexAllCenter;
                                @include fontSize(11px,300);
                                color: $mainColor;
                            }
                        }
                        .menuInfo {
                            p {
                                color: #333;
                                @include fontSize(14px,500);
                                line-height: 20px;
                                @include flexCenter;
                                @include wordClear;
                                width: 100%;
                            }
                        }
                        .price { 
                            position: absolute;
                            width: 100%;
                            bottom: -2px;
                            &:after {
                                @include clearBoth;
                            }                   
                            .priceNum {
                                float: right;
                                color: #333;
                                @include fontSize(12px,400);
                                span {
                                    color: #333;
                                    @include fontSize(15px,700);
                                    line-height: 18px;
                                }
                            }
                        }
                    }
                }
                .list-item {
                    padding: 15px 0;
                    border-bottom: 1px solid #eee;
                    &:first-of-type {
                        padding-top: 20px;
                    }
                    &:after {
                        @include clearBoth;
                    }
                    &.none-img {
                        .word-box {
                            width: 100%;
                            height: auto;
                            .toBottom {
                                position: inherit;
                                width: 100%;
                            }
                        }
                    }
                    .custom-bar {
                        margin-bottom: 5px;
                        &:after {
                            @include clearBoth;
                        }
                        ul {
                            float: right;
                            @include flexCenter;
                            li {
                                @include fontSize(11px,300);
                                color: $mainColor;
                                margin-right: 10px;
                                &:last-child {
                                    margin-right: 0;
                                }
                            }
                        }
                    }
                    .img-box {
                        background: url(../img/image_09.png) center center;
                        background-size: cover;
                        width: 97px;
                        height: 97px;
                        margin-right: 15px;
                        float: left;
                    }
                    .word-box {
                        float: left;
                        width: calc( 100% - 112px );
                        padding-left: 14px;
                        height: 97px;
                        position: relative;
                        &.showing {
                            height: inherit;
                            .toBottom {
                                position: inherit;
                                width: 100%;
                                .more-btn {
                                    transform: rotate(180deg);
                                }
                            }
                            .user-word {
                                max-height: inherit;
                                overflow: inherit;
                                display: inherit;
                                -webkit-box-orient: inherit;
                                -webkit-line-clamp: inherit;
                            }
                        }
                        >img {
                            position: absolute;
                            top: 0;
                            left: 0;
                            width: 10px;
                        }
                        .user-word {
                            color: #333;
                            @include fontSize(12px,400);
                            line-height: 20px;
                            max-height: 60px;
                            display: -webkit-box;
                            -webkit-box-orient: vertical;
                            -webkit-line-clamp: 3;
                            overflow: hidden;
                        }
                        .toBottom {
                            position: absolute;
                            bottom: -4px;
                            right: 0;
                            width: calc( 100% - 14px );
                            .user-info {
                                @include flexCenter;
                                .user-name {
                                    @include fontSize(10px,300);
                                    line-height: 18px;
                                    margin-right: 10px;
                                    strong,span {
                                        @include fontSize(12px,700);
                                    }
                                    strong {
                                        margin-right: 1px;
                                    }
                                }
                                .user-score {
                                    @include flexCenter;
                                    li {
                                        height: 19px;
                                        margin-right: 1px;
                                        padding-top: 2px;
                                        &:last-child {
                                            margin-right: 0;
                                        }
                                        img {
                                            width: 14px;
                                        }
                                    }
                                }
                            }
                            .item-subject {
                                @include fontSize(10px,400);
                                line-height: 15px;
                                width: 100%;
                                @include wordClear;
                            }
                            .more-btn {
                                @include btnNone;
                                @include flexAllCenter;
                                position: absolute;
                                right: 0;
                                top: 5px;
                                img {
                                    width: 13px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}