.fullPopup-wrapper {
	background: #fff;
	position: fixed;
	top: 0;
	right: -100%;
	width: 100%;
	height: 100vh;
	z-index: 11;
}

.fullPopup-wrapper.slideIn {
	right: 0;
	transition: .3s;
}

.fullPopup-wrapper.slideOut {
	right: -100%;
	transition: .3s;
}

