@keyframes tabLine {
	0% {
		width: 0;
	}
	100% {
		width: 100%;
	}
}

.fullPopup-wrapper .tab-section > ul > li:before {
	content: "";
	position: absolute;
	right: 0;
	background: #edf0f3;
	height: 9px;
	width: 1px;
	top: 50%;
	transform: translate(0, -50%);
}

.fullPopup-wrapper .tab-section > ul > li.active {
	font-size: 15px;
	font-weight: 700;
	color: #000;
	translate: .3s;
}

.fullPopup-wrapper .tab-section > ul > li.active:after {
	content: "";
	width: 100%;
	position: absolute;
	bottom: -1px;
	height: 3px;
	background: #000;
	left: 50%;
	transform: translate(-50%, 0);
	animation: tabLine .3s;
}

.fullPopup-wrapper .tab-section > ul > li:last-child:before {
