.content-body {
    &.ppGoodsDetail-content {
        padding: 54px 0 60px;
        .goods-img {
            .swiper-wrapper {
                .swiper-slide {
                    img {
                        width: 100%;
                    }
                }
            }
            .swiper-pagination {
                .swiper-pagination-bullet {
                    opacity: 1;
                    background: rgba(255,255,255,.8);
                    height: 6px;
                    width: 6px;
                }
                .swiper-pagination-bullet-active {
                    background: $mainColor;
                    width: 18px;
                    border-radius: 18px;
                    transition: .3s;
                }
            }
        }
        .goodsInfo-container {
            padding: 20px 15px;
            >span {
                display: inline-block;
                color: #fff;
                @include fontSize(12px,300);
                line-height: 24px;
                height: 23px;
                background: #000;
                padding: 0 9px;
                border-radius: 99px;
                margin-bottom: 5px;
            }
            .goods-subject {
                @include fontSize(17px,500);
                line-height: 25px;
            }
            .goods-price {
                line-height: 28px;
                p {
                    >* {
                        margin-right: 5px;
                        &:last-child {
                            margin-right: 0;
                        }
                    }
                    .percent,.percent span {
                        color: $mainColor;
                        @include fontSize(23px,700);
                        line-height: 28px;
                    }
                    .price {
                        color: #333;
                        @include fontSize(14px,500);
                        line-height: 18px;
                        span {
                            color: #333;
                            @include fontSize(18px,700);
                        }
                    }
                    small {
                        color: #999;
                        @include fontSize(13px,300);
                        line-height: 15px;
                        text-decoration-line: line-through;
                    }
                }
            }
            .goods-count {
                margin-bottom: 11px;
                p,span {
                    @include fontSize(12px,300);
                    color: #999;
                    line-height: 18px;
                }
            }
            .star-score {
                @include flexCenter;
                li {
                    &.counting {
                        @include fontSize(17px,700);
                        color: #fed220;
                        margin-right: 2px;
                    }
                    img {
                        width: 19px;
                    }
                }
            }
        }
        .purchase-footer {
            &.outOfStock {
                .subBtn-wrap {
                    border-top: 1px solid rgba(#666,.1);
                    float: left;
                    width: 115px;
                    @include flexCenter;
                    button {
                        width: 50%;
                        position: relative;
                        background: #fff;
                        &:after {
                            background: rgba(#666,.1);
                        }
                    }
                }
                .mainBtn-wrap {
                    button {
                        background: #666;
                    }
                }
            }
            button {
                @include btnNone;
                height: 60px;
            }
            .subBtn-wrap {
                border-top: 1px solid rgba($mainColor,.1);
                float: left;
                width: 115px;
                @include flexCenter;
                button {
                    width: 50%;
                    position: relative;
                    background: #fff;
                    &:after {
                        content: "";
                        width: 1px;
                        height: 20px;
                        position: absolute;
                        right: 0;
                        top: 50%;
                        transform: translate(0,-50%);
                        background: rgba($mainColor,.1);
                    }
                    &:last-of-type {
                        &:after {
                            display: none;
                        }
                    }
                    img {
                        width: 22px;
                    }
                }
            }
            .mainBtn-wrap {
                width: calc( 100% - 115px );
                button {
                    background: $mainColor;
                    color: #fff;
                    @include fontSize(15px,500);
                    width: 100%;
                }
            }
        }
        .goodsTab-container {
            .small-list {
                &.active {
                    position: fixed;
                    top: 54px;
                    z-index: 1;
                    width: 100%;
                    li {
                        background: #fff;
                    }
                }
            }
            .target-wrap {
                position: relative;
                .tabTarget {
                    padding: 20px 15px 30px;
                    &.over-size {
                        padding: 20px 0 30px;
                    }
                    &.scrolled {
                        padding-top: 64px;
                    }
                    .write-btn {
                        height: 42px;
                        background: #fff;
                        border: 1px solid $mainColor;
                        width: 100%;
                        @include fontSize(12px,500);
                        color: $mainColor;
                        margin-bottom: 20px;
                    }
                    .goods {
                        width: 100%;
                    }
                    .accordian-list {
                        >ul {
                            >li {
                                @include fontSize(12px,400);
                                color: #333;
                                line-height: 18px;
                                padding: 13px 15px;
                                position: relative;
                                border-bottom: 1px solid #eee;
                                &:first-child {
                                    border-top: 1px solid #eee;
                                }
                                img {
                                    position: absolute;
                                    right: 15px;
                                    top: 50%;
                                    transform: translate(0,-50%);
                                    width: 13px;
                                }
                            }
                        }
                    }
                    .review-wrapper {
                        .review-container {
                            .none-list {
                                padding: 30px 0 20px;
                                text-align: center;
                                @include fontSize(12px,500);
                                color: #999;
                                line-height: 18px;
                            }
                            .review-list {
                                margin-bottom: 20px;
                                &:last-child {
                                    margin-bottom: 0;
                                }
                                .review-title {
                                    margin-bottom: 20px;
                                    &:after {
                                        @include clearBoth;
                                    }
                                    p {
                                        line-height: 20px;
                                        @include fontSize(12px,700);
                                        color: #333;
                                        float: left;
                                        span {
                                            line-height: 20px;
                                            @include fontSize(12px,700);
                                            color: #333;
                                        }
                                    }
                                    button {
                                        float: right;
                                        @include flexAllCenter;
                                        @include btnNone;
                                        @include fontSize(12px,400);
                                        color: #333;
                                        img {
                                            margin-left: 5px;
                                        }
                                    }
                                }
                                .review-content {
                                    &.photo-review {
                                        @include flexCenter;
                                        .img-box {
                                            width: calc( (100% - 21px)/4 );
                                            height: 77px;
                                            background: url(../img/image_04.png) center center;
                                            background-size: cover;
                                            margin-right: 7px;
                                            &:last-child {
                                                margin-right: 0;
                                            }
                                        }
                                    }
                                    .list-item {
                                        padding: 15px 0;
                                        border-bottom: 1px solid #eee;
                                        &:first-of-type {
                                            padding-top: 0;
                                        }
                                        &:after {
                                            @include clearBoth;
                                        }
                                        &.none-img {
                                            .word-box {
                                                width: 100%;
                                                height: auto;
                                                .toBottom {
                                                    position: inherit;
                                                    width: 100%;
                                                }
                                            }
                                        }
                                        .img-box {
                                            background: url(../img/image_09.png) center center;
                                            background-size: cover;
                                            width: 97px;
                                            height: 97px;
                                            margin-right: 15px;
                                            float: left;
                                        }
                                        .word-box {
                                            float: left;
                                            width: calc( 100% - 112px );
                                            padding-left: 14px;
                                            height: 97px;
                                            position: relative;
                                            &.showing {
                                                height: inherit;
                                                .toBottom {
                                                    position: inherit;
                                                    width: 100%;
                                                    .more-btn {
                                                        transform: rotate(180deg);
                                                    }
                                                }
                                                .user-word {
                                                    max-height: inherit;
                                                    overflow: inherit;
                                                    display: inherit;
                                                    -webkit-box-orient: inherit;
                                                    -webkit-line-clamp: inherit;
                                                }
                                            }
                                            >img {
                                                position: absolute;
                                                top: 0;
                                                left: 0;
                                                width: 9px;
                                            }
                                            .user-word {
                                                color: #333;
                                                @include fontSize(12px,400);
                                                line-height: 20px;
                                                max-height: 60px;
                                                display: -webkit-box;
                                                -webkit-box-orient: vertical;
                                                -webkit-line-clamp: 3;
                                                overflow: hidden;
                                            }
                                            .toBottom {
                                                position: absolute;
                                                bottom: -4px;
                                                right: 0;
                                                width: calc( 100% - 14px );
                                                .user-info {
                                                    @include flexCenter;
                                                    .user-name {
                                                        @include fontSize(10px,300);
                                                        line-height: 18px;
                                                        margin-right: 10px;
                                                        strong,span {
                                                            @include fontSize(12px,700);
                                                        }
                                                        strong {
                                                            margin-right: 1px;
                                                        }
                                                    }
                                                    .user-score {
                                                        @include flexCenter;
                                                        li {
                                                            height: 19px;
                                                            margin-right: 1px;
                                                            padding-top: 2px;
                                                            width: 14px;
                                                            &:last-child {
                                                                margin-right: 0;
                                                            }
                                                            img {
                                                                width: 14px;
                                                            }
                                                        }
                                                    }
                                                }
                                                .item-subject {
                                                    @include fontSize(10px,400);
                                                    line-height: 15px;
                                                    width: 100%;
                                                    @include wordClear;
                                                }
                                                .more-btn {
                                                    @include btnNone;
                                                    @include flexAllCenter;
                                                    position: absolute;
                                                    right: 0;
                                                    top: 5px;
                                                    img {
                                                        width: 13px;
                                                    }
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                    .qna-wrapper {
                        .write-btn {
                            width: calc( 100% - 30px );
                            margin: 0 auto 20px;
                            display: block;
                        }
                        .qna-container {
                            .none-list {
                                padding: 30px 0 20px;
                                text-align: center;
                                @include fontSize(12px,500);
                                color: #999;
                                line-height: 18px;
                            }
                            .qna-folder {
                                padding: 15px;
                                border-bottom: 1px solid #eee;
                                &.open {
                                    background: #f5f5f5;
                                    &:first-child {
                                        padding-top: 15px;
                                    }
                                    .qna-managerContent {
                                        display: block;
                                    }
                                    .more-btn {
                                        img {
                                            transform: rotate(180deg);
                                        }
                                    }
                                }
                                &:first-child {
                                    padding-top: 0;
                                }
                                &.clear-list {
                                    .qna-userContent {
                                        .qna-header {
                                            .status-box {
                                                >span {
                                                    background: $mainColor;
                                                    color: #fff;
                                                }
                                            }
                                            .etc-box {
                                                .more-btn {
                                                    @include flexAllCenter;
                                                }
                                            }
                                        }
                                    }
                                }
                                .qna-userContent {
                                    .qna-header {
                                        margin-bottom: 15px;
                                        .status-box {
                                            @include flexCenter;
                                            margin-bottom: 5px;
                                            >span {
                                                @include fontSize(10px,400);
                                                color: $mainColor;
                                                background: rgba($mainColor,.2);
                                                padding: 5px 6px;
                                                display: inline-block;
                                                margin-right: 5px;
                                            }
                                            p {
                                                @include fontSize(10px,300);
                                                strong,span {
                                                    @include fontSize(12px,800);
                                                    line-height: 20px;
                                                }
                                                strong {
                                                    margin-right: 1px;
                                                }
                                            }
                                        }
                                        .etc-box {
                                            @include flexCenter;
                                            position: relative;
                                            .category {
                                                margin-right: 5px;
                                            }
                                            .category,.category span {
                                                line-height: 18px;
                                                @include fontSize(12px,500);
                                                color: #999;
                                            }
                                            .category span {
                                                margin: 0 1px;
                                            }
                                            .date {
                                                @include fontSize(12px,400);
                                                color: #333;
                                            }
                                            .more-btn {
                                                display: none;
                                                @include btnNone;
                                                position: absolute;
                                                right: 0;
                                                top: 50%;
                                                transform: translate(0,-50%);
                                                img {
                                                    width: 13px;
                                                }
                                            }
                                        }
                                    }
                                    .qna-content {
                                        &.secret {
                                            img {
                                                width: 16px;
                                                margin-right: 5px;
                                            }
                                            pre {
                                                @include flexCenter;
                                            }
                                        }
                                        pre {
                                            @include fontSize(12px,400);
                                            line-height: 20px;
                                            color: #333;
                                            word-break: keep-all;
                                        }
                                    }
                                }
                                .qna-managerContent {
                                    margin-top: 15px;
                                    display: none;
                                    span {
                                        display: block;
                                        margin-bottom: 5px;
                                        @include fontSize(12px,500);
                                        color: $mainColor;
                                    }
                                    pre {
                                        @include fontSize(12px,500);
                                        line-height: 20px;
                                        color: #333;
                                        word-break: keep-all;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}