.content-body {
    &.productSearch {
        padding-bottom: 0;
        .search-container {
            margin: 10px 0 20px;
            .word-box {
                margin-bottom: 15px;
                span {
                    @include fontSize(12px,400);
                    color: #000;
                    line-height: 18px;
                    &.colorNum {
                        @include fontSize(14px,700);
                        line-height: 17px;
                        color: $mainColor;
                    }
                }
            }
            .btn-box {
                position: relative;
                &:after {
                    @include clearBoth;
                }
                .order-item {
                    float: left;
                    width: 100%;
                    input {
                        width: 100%;
                        padding: 0 10px;
                        height: 35px;
                        border: none;
                        line-height: 22px;
                        border-bottom: 1px solid rgba(#000, .2);
                        @include fontSize(15px,400); 
                        color: #000;
                    }
                    ::placeholder {
                        color: #c4c4c4;
                        @include fontSize(14px,300);
                    }
                }
                button {
                    position: absolute;
                    right: 10px;
                    top: 50%;
                    transform: translate(0,-50%);
                    @include flexAllCenter;
                    height: 15px;
                    width: 15px;
                    @include btnNone;
                    img {
                        width: 15px;
                    }
                }
            }
        }
        .goods-container {
            overflow: scroll;
            height: calc( 100vh - 119px );
            .none-list {
                @include fontSize(12px,500);
                color: #999;
                text-align: center;
                line-height: calc( 100vh - 119px );
            }
            .content-box {                    
                .distance-area {
                    text-align: right;
                    margin-bottom: 10px;
                    color: #333;
                    @include fontSize(10px,300);
                    line-height: 15px;
                }
                .content-area {
                    overflow: hidden;
                    &:after {
                        @include clearBoth;
                    } 
                    .contentItem {
                        float: left;
                        width: calc( (100% - 20px) / 3 );                            
                        margin-bottom: 20px;
                        margin-right: 10px;
                        &:nth-child(3n) {
                            margin-right: 0;
                        }                                     
                        .img-box {
                            background: url(../img/image_01.png) center center no-repeat;
                            height: 100px; 
                            margin-bottom: 6px;
                            position: relative;
                        }
                        p {
                            .storeTxt {
                                @include fontSize(10px,700);
                                color: $mainColor;
                                line-height: 15px;
                                margin-right: 4.63%;
                                max-width: 40.741%;
                                @include wordClear;
                            }
                            .priceTxt {
                                @include fontSize(15px,700);
                                color: #333;
                                line-height: 18px;
                                max-width: 54.63%;
                                @include wordClear;
                            }
                        }
                        .titleBlock {
                            @include fontSize(12px,400);
                            color: #333;
                            line-height: 18px;
                            @include wordClear;
                            width: 100%;
                        }
                    }
                }
            }
        }
    }
}