.content-body {
    &.pickupOrder {
        padding: 54px 0 0;
        .goods-container {
            padding: 10px 15px 25px;
            .container-title {
                @include fontSize(14px,700);
                line-height: 23px;
                margin-bottom: 10px;
            } 
            .container-content {
                border-bottom: 1px solid #eeeeee;    
                &:after {
                    @include clearBoth;
                }
                &:last-child{
                    border-bottom: 0;            
                }        
                .img-area {
                    background: url(../img/image_02.png) center center;
                    width: 80px;
                    height: 80px;
                    margin-right: 10px;        
                    float: left;          
                }
                .word-area {
                    width: calc( 100% - 90px);
                    float: right;
                    .top-box {      
                        margin-bottom: 7px;
                        p {
                            @include fontSize(11px,300);
                            color: #333333;
                            line-height: 14px;
                            span {
                                color: #fff;
                                @include fontSize(12px,300);
                                background: #000;
                                padding: 0 9px;
                                line-height: 24px;
                                border-radius: 12px;
                                display: inline-block;
                            }
                        }
                    }
                    .menu-box {
                        margin-bottom: 12px;
                        p {
                            color: #333;
                            @include fontSize(14px,500);
                            line-height: 22px;
                            @include flexCenter;
                        }
                    }
                    .price-box { 
                        float: right;                        
                        &:after {
                            @include clearBoth;
                        }                   
                        .priceNum {
                            float: left;
                            color: #333;
                            @include fontSize(12px,400);
                            margin-right: 5px;
                            line-height: 18px;
                            span {
                                color: #333;
                                @include fontSize(15px,700);
                                line-height: 18px;
                            }
                        }
                        .pricePer {
                            float: right;
                            color: #333;
                            @include fontSize(12px,400);
                            line-height: 18px;
                            span {
                                color: #333;
                                @include fontSize(15px,700);
                                line-height: 18px;
                            }
                        }
                    }
                }
            }
        }
        .user-container {
            padding: 25px 15px;
            .container-title {
                @include fontSize(14px,700);
                line-height: 23px;
                margin-bottom: 10px;
            }
            .input-wrap {
                width: 100%;
                margin-bottom: 25px;
                &:last-child {
                    margin-bottom: 0;
                }
                .input-title {
                    @include fontSize(14px, 500);
                    color: #000;
                    margin-bottom: 5px;                        
                }
                .input-content {
                    width: 100%;
                    &.phoneNum {
                        @include flexCenter;
                        .input-box {
                            width: calc( (100% - 60px)/3 );
                            margin-right: 30px;
                            position: relative;
                            &:last-of-type {
                                margin-right: 0;
                                &:after {
                                    display: none;
                                }
                            }
                            &:after {
                                content: "-";
                                position: absolute;
                                right: -17px;
                                top: 50%;
                                transform: translate(0,-50%);
                                @include fontSize(14px,400);
                            }
                            input {
                                text-align: center;
                            }
                        }
                    }
                    &.eMail {
                        @include flexCenter;
                        .input-box {
                            margin-right: 30px;
                            width: 42.424%;
                            &:last-of-type {
                                margin-right: 0;
                                width: 48.485%;
                                &::after {
                                    display: none;
                                }
                            }
                            &:after {
                                content: "@";
                                position: absolute;
                                right: -22px;
                                top: 50%;
                                transform: translate(0,-50%);
                                @include fontSize(14px,400);
                            }
                            .selboxDirect {
                                background: none;
                                padding-right: 30px;
                            }
                            select {
                                border: 0;
                                width: 100%;
                                height: 35px;
                                @include fontSize(15px,500);
                                line-height: 18px;
                                color: #000;
                                border-bottom: 1px solid rgba(#000, .2);
                                padding: 0 10px;
                                &.hideing {
                                    width: 35px;
                                    height: 35px;
                                    position: absolute;
                                    right: 0;
                                    top: 0;
                                    opacity: 0;
                                }
                            }
                        }
                    }
                    .input-box {
                        position: relative;
                        img {
                            position: absolute;
                            right: 10px;
                            top: 50%;
                            transform: translate(0,-50%);
                            width: 13px;
                            z-index: -1;
                        }
                        .fake-text {
                            position: absolute;
                            right: 10px;
                            top: 50%;
                            transform: translate(0,-50%);
                        }
                        input,select {
                            width: 100%;
                            padding: 0 10px;
                            height: 35px;
                            border: none;
                            border-bottom: 1px solid rgba(#000, .2);
                            @include fontSize(15px,500);
                            &.noneVal {
                                color: #c4c4c4;
                                @include fontSize(14px,300);
                                line-height: 23px;
                            }  
                        }
                    }
                }
            }
            .container-table {
                li {
                    margin-bottom: 5px;
                    &:after {
                        @include clearBoth;
                    }
                    &.table-total {
                        margin: 10px 0 0;
                        padding-top: 10px;
                        border-top: 1px solid #eee;
                        position: relative;
                        .table-title {
                            position: absolute;
                            left: 0;
                            bottom: 0;
                        }
                        .table-text {
                            color: $mainColor;
                            span {
                                color: $mainColor;
                                @include fontSize(23px,700);
                                line-height: 28px;
                                margin-right: 2px;
                            }
                        }
                    }
                    .table-title {
                        float: left;
                        @include fontSize(14px,400);
                        color: #333;
                        line-height: 20px;
                    }
                    .table-text {
                        float: right;
                        @include fontSize(14px,400);
                        line-height: 20px;
                        span {
                            @include fontSize(14px,400);
                            line-height: 20px;
                        }
                    }
                }
            }
            .alert-msg {
                margin: 20px 0 30px;
                >span {
                    @include fontSize(14px,700);
                    color: $mainColor;
                    line-height: 20px;
                    margin-bottom: 5px;
                    display: block;
                }
                ul {
                    li {
                        padding-left: 10px;
                        position: relative;
                        @include fontSize(14px,400);
                        color: #333;
                        line-height: 22px;
                        word-break: keep-all;
                        &:after {
                            content: "-";
                            position: absolute;
                            top: 0;
                            left: 0;
                        }
                        span {
                            color: $mainColor;
                            @include fontSize(14px,500);
                        }
                    }
                }
            }
            .checkbox-wrap {
                position: relative;
                margin-bottom: 5px;
                .checkbox-custom {
                    position: absolute;
                    left: 0;
                    top: 1px;
                }
                .checkbox-label {
                    width: 100%;
                    display: block;
                    padding-left: 20px;
                }
                .checkboxColor {
                    color: $mainColor;
                    @include fontSize(12px,300);
                }
                input[type="checkbox"] {
                    display:none;
                }
                input[type="checkbox"] + label .checkbox-custom {
                    display:inline-block;
                    width: 15px;
                    height: 15px;
                    vertical-align:middle;
                    cursor:pointer;
                    border: 1px solid #c9c9c9;
                    background: #fff;
                    -webkit-background-size: 100%; background-size: 100%;
                }
                input[type="checkbox"]:checked + label .checkbox-custom {
                    width: 15px;
                    height: 15px;
                    border: none;
                    background: url(../img/icon_checkbox_on.png) center center no-repeat;
                    -webkit-background-size: 100%; background-size: 100%;
                }
                .checkbox-label {
                    @include fontSize(12px,300);
                    line-height: 18px;
                }
            }
        }
        .btn-container {
            @include flexCenter;
            button {
                border: 0;
                height: 60px;
                width: 50%;
                color: #fff;
                @include fontSize(15px,500);                       
                line-height: 22px;
                background: #666;
                &.okBtn {
                    background: $mainColor;
                }
            }
        }
    }
}