.content-body {
    &.order {
        padding: 54px 0 60px;
        .goods-container {
            padding: 10px 15px 25px;
            .container-title {
                @include fontSize(14px,700);
                line-height: 23px;
                margin-bottom: 10px;
            }            
            .container-content {
                border-bottom: 1px solid #eeeeee;
                &:after {
                    @include clearBoth;
                }
                &:last-child{
                    border-bottom: 0;
                }        
                .img-wrapper {
                    background: url(../img/image_09.png) center center;
                    width: 80px;
                    height: 80px;
                    margin-right: 10px;
                    float: left;    
                }
                .word-wrapper {
                    width: calc( 100% - 90px);
                    float: right;
                    .top-area {      
                        margin-bottom: 10px; 
                        position: relative;
                        @include fontSize(11px,300);
                        color: #333333;
                        line-height: 14px;
                        span {
                            color: #fff;
                            @include fontSize(12px,300);
                            line-height: 24px;
                            background: #000;
                            padding: 0px 9px;
                            border-radius: 12px;
                            display: inline-block;
                        }
                        button {  
                            border: 1px solid rgba(#000, 0.2);
                            background: #fff;
                            position: absolute;
                            top: 0;
                            right: 0;
                            height: 28px;
                            width: 60px;
                            @include flexAllCenter;
                            color: #333;
                            @include fontSize(11px,300);
                        }
                    }
                    .menu-area {
                        margin-bottom: 12px;
                        p {
                            color: #333;
                            @include fontSize(14px,500);
                            line-height: 22px;
                            @include flexCenter;
                        }
                    }
                    .bottom-area {
                        width: 100%;
                        &:after {
                            @include clearBoth;
                        }
                        .goodsStatus-box {
                            float: left;
                            &:after {
                                @include clearBoth;
                            }  
                            p {
                                @include fontSize(12px,500);
                                line-height: 18px;
                            }
                            .paymentComplete {
                                float: left;
                                color: rgba($mainColor, .4);
                                margin-right: 5px;
                                text-decoration: line-through;
                            }
                            .paymentCancle {
                                float: right;
                                color: #e52222;
                            }
                        }
                        .price-box { 
                            float: right;
                            &:after {
                                @include clearBoth;
                            }
                            .priceNum {
                                float: left;
                                color: #333;
                                @include fontSize(12px,400);
                                margin-right: 5px;
                                line-height: 18px;
                                span {
                                    color: #333;
                                    @include fontSize(15px,700);
                                    line-height: 18px;
                                }
                            }
                            .pricePer {
                                float: right;
                                color: #333;
                                @include fontSize(12px,400);
                                line-height: 18px;
                                span {
                                    color: #333;
                                    @include fontSize(15px,700);
                                    line-height: 18px;
                                }
                            }
                        }
                    }
                }
            }
        }
        .user-container {
            padding: 25px 15px;
            .container-title {
                @include fontSize(14px,700);
                line-height: 20px;
                margin-bottom: 10px;
                color: #000;
            }
            .container-table {
                padding-bottom: 10px;
                margin-bottom: 10px;
                border-bottom: 1px solid #eee;
                &:last-child {
                    border-bottom: 0;
                    margin-bottom: 0;
                    padding-bottom: 0;
                }
                li {
                    margin-bottom: 5px;
                    &:last-of-type {
                        margin-bottom: 0;
                    }
                    &:after {
                        @include clearBoth;
                    }
                    &.table-total {
                        position: relative;
                        .table-title {
                            position: absolute;
                            left: 0;
                            bottom: 0;
                        }
                        .table-text {
                            span {
                                &.bigText {
                                    color: $mainColor;
                                    @include fontSize(23px,700);
                                    line-height: 28px;
                                    margin-right: 2px;
                                }
                            }
                        }
                    }
                    .table-title {
                        @include fontSize(14px,400);
                        color: #333;
                        line-height: 20px;
                        float: left;
                        width: 94px;
                        margin-right: 60px;
                    }
                    .table-text {
                        @include fontSize(14px,500);
                        color: #000;
                        line-height: 20px;
                        float: right;
                        width: calc( 100% - 154px );
                        text-align: right;
                        &.colorText {
                            @include fontSize(12px,400);
                            line-height: 18px;
                            color: $mainColor;
                        }
                        &.lightPrice {
                            @include fontSize(14px,400);                            
                        }
                        span {
                            @include fontSize(14px,400);
                            line-height: 17px;
                            &.boldPrice {
                                @include fontSize(14px,700);
                            }
                            &.mediumPrice {
                                @include fontSize(14px,500);
                            }
                        }
                    }  
                }
            }
        }
        .btn-area {
            .okBtn {
                border: 0;
                height: 60px;
                width: 100%;
                background: $mainColor;
                color: #fff;
                @include fontSize(15px,500);
            }
        }
    }
}