.content-body {
    &.ppMain-content {
        .swiper-container {
            &.sub-content {
                margin-bottom: 33px;
            }
            .moreTitle-wrapper {
                @include flexCenter;
                position: relative;
                margin-bottom: 16px;
                >img {
                    margin-right: 5px;
                    height: 19px;
                }
                p {
                    @include fontSize(17px,700);
                    line-height: 25px;
                }
                button {
                    position: absolute;
                    right: 0;
                    top: 0;
                    height: 25px;
                    @include flexCenter;
                    @include btnNone;
                    img {
                        width: 16px;
                    }
                }
            }
        }
        .mainSlide-container {
            margin-bottom: 18px;
            .swiper-wrapper {
                .swiper-slide {
                    height: 376px;
                    background: url(../img/image_main.png) center center;
                    background-size: cover;
                    position: relative;
                    .slogun-box {
                        position: absolute;
                        left: 35px;
                        bottom: 83px;
                        ul {
                            margin-bottom: 15px;
                            li {
                                line-height: 40px;
                                @include fontSize(33px,700);
                                color: #fff;
                            }
                        }
                        p {
                            color: #fff;
                            line-height: 25px;
                            @include fontSize(15px,300);
                        }
                    }
                }
            }
            .swiper-pagination {
                width: calc( 100% - 30px );
                &.swiper-pagination-progressbar {
                    top: inherit;
                    bottom: 40px;
                    left: 30px;
                    height: 3px;
                    background: rgba(255,255,255,.5);
                    .swiper-pagination-progressbar-fill {
                        background: #fff;
                    }
                }
            }
        }
        .storeTitle-container {
            padding: 0 15px;
            margin-bottom: 20px;
            .ico-wrapper {
                @include flexCenter;
                button {
                    @include btnNone;
                    @include flexAllCenter;
                    margin-right: 5px;
                    &:last-of-type {
                        margin-right: 0;
                    }
                    img {
                        width: 22px;
                    }
                }
                .counting-btn {
                    position: relative;
                    span {
                        display: inline-block;
                        position: absolute;
                        top: -3px;
                        right: -7px;
                        @include fontSize(10px,700);
                        color: #fff;
                        background: $mainColor;
                        padding: 0px 6px;
                        border-radius: 99px;
                    }
                }
            }
            .title-wrapper {
                .main-title {
                    &:after {
                        @include clearBoth;
                    }
                    p {
                        float: left;
                        @include fontSize(23px,700);
                        line-height: 34px;
                        max-width: calc( 100% - 110px );
                        @include wordClear;
                    }
                    ul {
                        margin-left: 10px;
                        padding-top: 7px;
                        @include flexCenter;
                        float: left;
                        li {
                            border: 1px solid #000;
                            padding: 1px 3px;
                            margin-right: 5px;
                            @include fontSize(11px,300);
                            line-height: 17px;
                            &:last-of-type {
                                margin-right: 0;
                            }
                            &.gray-link {
                                border-color: rgba(0,0,0,.2);
                                color: #333;
                            }
                            &.red-link {
                                border-color: rgba($mainColor,.5);
                                color: $mainColor;
                            }
                        }
                    }
                }
                .sub-title {
                    ul {
                        @include flexCenter;
                        li {
                            line-height: 18px;
                            @include fontSize(12px,400);
                            color: #999;
                            margin-right: 21px;
                            position: relative;
                            &:after {
                                content: "";
                                position: absolute;
                                right: -11px;
                                width: 1px;
                                height: 7px;
                                background: #999;
                                top: 50%;
                                transform: translate(0,-50%);
                            }
                            &:last-of-type {
                                margin-right: 0;
                                &:after {
                                    display: none;
                                }
                            }
                        }
                    }
                }
            }
        }
        .itemSlide-container {
            padding: 0 20px;
            .swiper-wrapper {
                .swiper-slide {
                    width: 150px;
                    .img-box {
                        margin-bottom: 10px;
                        background: url(../img/image_01.png) center center;
                        background-size: cover;
                        height: 150px;
                        position: relative;
                        img {
                            position: absolute;
                            right: 0;
                            bottom: 0;
                            width: 25px;
                        }
                    }
                    .price-box {
                        margin-bottom: 3px;
                        p {
                            span {
                                line-height: 18px;
                                @include fontSize(15px,700);
                                &.sale-word {
                                    color: $mainColor;
                                    margin-right: 5px;
                                }
                            }
                            small {
                                @include fontSize(10px,300);
                                text-decoration-line: line-through;
                                color: #333;
                                text-decoration-color: #333;
                                margin-left: 2px;
                            }
                        }
                    }
                    .item-subject {
                        p {
                            width: 100%;
                            line-height: 18px;
                            color: #333;
                            @include fontSize(12px,400);
                            @include wordClear;
                        }
                    }
                }
            }
        }
        .wideSlide-container {
            padding: 0 20px;
            .swiper-wrapper {
                .swiper-slide {
                    width: 290px;
                    position: relative;
                    .img-box {
                        background: url(../img/image_04.png) center center;
                        background-size: cover;
                        height: 230px;
                        position: relative;
                        img {
                            position: absolute;
                            right: 0;
                            bottom: 0;
                            width: 25px;
                        }
                    }
                    .word-box {
                        position: absolute;
                        left: 0;
                        bottom: 0;
                        background: #fff;
                        padding: 10px 14px 5px 5px;
                        max-width: 180px;
                        .price-box {
                            margin-bottom: 3px;
                            p {
                                span {
                                    line-height: 18px;
                                    @include fontSize(15px,700);
                                    &.sale-word {
                                        color: $mainColor;
                                        margin-right: 5px;
                                    }
                                }
                                small {
                                    @include fontSize(10px,300);
                                    text-decoration-line: line-through;
                                    color: #333;
                                    text-decoration-color: #333;
                                    margin-left: 2px;
                                }
                            }
                        }
                        .item-subject {
                            p {
                                width: 100%;
                                line-height: 18px;
                                color: #333;
                                @include fontSize(12px,400);
                                @include wordClear;
                            }
                        }
                    }
                }
            }
        }
        .cubeItem-container {
            padding: 0 20px;
            .cubeBox-wrapper {
                @include flexCenter;
                .cubeItem {
                    width: calc( (100% - 20px)/3 );
                    &:last-of-type {
                        margin-right: 0;
                    }
                    .img-box {
                        margin-bottom: 10px;
                        background: url(../img/image_06.png) center center;
                        background-size: cover;
                        position: relative;
                        img {
                            position: absolute;
                            right: 0;
                            bottom: 0;
                            width: 25px;
                        }
                    }
                    .price-box {
                        margin-bottom: 3px;
                        p {
                            span {
                                line-height: 18px;
                                @include fontSize(15px,700);
                                &.sale-word {
                                    color: $mainColor;
                                    margin-right: 5px;
                                }
                            }
                            small {
                                @include fontSize(10px,300);
                                text-decoration-line: line-through;
                                color: #333;
                                text-decoration-color: #333;
                                margin-left: 2px;
                            }
                        }
                    }
                    .item-subject {
                        p {
                            width: 100%;
                            line-height: 18px;
                            color: #333;
                            @include fontSize(12px,400);
                            @include wordClear;
                        }
                    }
                }
            }
        }
        .reviewSlide-container {
            padding: 0 20px;
            .swiper-wrapper {
                .swiper-slide {
                    width: 310px;
                    position: relative;
                    .review-list {
                        padding: 15px 0;
                        border-bottom: 1px solid #eee;
                        &:last-of-type {
                            border-bottom: 0;
                        }
                        &:first-of-type {
                            padding-top: 0;
                        }
                        &:after {
                            @include clearBoth;
                        }
                        .img-box {
                            background: url(../img/image_09.png) center center;
                            background-size: cover;
                            width: 97px;
                            height: 97px;
                            margin-right: 15px;
                            float: left;
                        }
                        .word-box {
                            float: left;
                            width: calc( 100% - 112px );
                            padding-left: 14px;
                            height: 97px;
                            position: relative;
                            >img {
                                position: absolute;
                                top: 0;
                                left: 0;
                                width: 9px;
                            }
                            .user-word {
                                display: -webkit-box;
                                -webkit-box-orient: vertical;
                                -webkit-line-clamp: 3;
                                overflow: hidden;
                                color: #333;
                                @include fontSize(12px,400);
                                line-height: 20px;
                            }
                            .toBottom {
                                position: absolute;
                                bottom: -4px;
                                right: 0;
                                width: calc( 100% - 14px );
                                .user-name {
                                    @include fontSize(10px,300);
                                    line-height: 18px;
                                    strong,span {
                                        @include fontSize(12px,700);
                                    }
                                    strong {
                                        margin-right: 1px;
                                    }
                                }
                                .item-subject {
                                    @include fontSize(10px,400);
                                    line-height: 15px;
                                    width: 100%;
                                    @include wordClear;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}


.fullPopup-wrapper {
    // display: none;
    background: #fff;
    position: fixed;
    top: 0;
    right: -100%;
    width: 100%;
    height: 100vh;
    z-index: 11;
    &.slideIn {
        right: 0;
        transition: .3s;
    }
    &.slideOut {
        right: -100%;
        transition: .3s;
    }
    .header-section {
        height: 54px;
        padding-left: 15px;
        @include flexCenter;
        position: relative;
        p {
            line-height: 29px;
            @include fontSize(20px,500);
        }
        button {
            position: absolute;
            right: 10px;
            top: 50%;
            transform: translate(0,-50%);
            @include btnNone;
            width: 29px;
            height: 29px;
            @include flexAllCenter;
            img {
                width: 19px;
            }
        }
    }
    .tab-section {
        >ul {
            @include flexCenter;
            border-bottom: 1px solid #eee;
            >li {
                text-align: center;
                height: 42px;
                width: calc( 100%/3 );
                @include flexAllCenter;
                @include fontSize(15px,400);
                color: #999;
                position: relative;
                @keyframes tabLine {
                    0% {
                        width: 0;
                    }
                    100% {
                        width: 100%;
                    }
                }
                &:before {
                    content: "";
                    position: absolute;
                    right: 0;
                    background: #edf0f3;
                    height: 9px;
                    width: 1px;
                    top: 50%;
                    transform: translate(0,-50%);
                }
                &.active {
                    @include fontSize(15px,700);
                    color: #000;
                    translate: .3s;
                    &:after {
                        content: "";
                        width: 100%;
                        position: absolute;
                        bottom: -1px;
                        height: 3px;
                        background: #000;
                        left: 50%;
                        transform: translate(-50%,0);
                        animation: tabLine .3s;
                    }
                }
                &:last-child {
                    &:before {
                        display: none;
                    }
                }
            }
        }
        .target-wrap {
            padding: 25px 15px;
            .tabTarget {
                display: none;
                &.active {
                    display: block;
                }
                >ul {
                    @include flexCenter;
                    margin-bottom: 20px;
                    >li {
                        width: calc( (100% - 20px )/3 );
                        position: relative;
                        border-bottom: 1px solid rgba(0,0,0,.2);
                        margin-right: 10px;
                        &.full-size {
                            width: 100%;
                        }
                        &:last-of-type {
                            margin-right: 0;
                        }
                        >img {
                            position: absolute;
                            right: 8px;
                            top: 50%;
                            transform: translate(0,-50%);
                            width: 10px;
                        }
                        select {
                            width: calc( 100% - 28px );
                            border: 0;
                            color: #000;
                            @include fontSize(15px,500);
                            line-height: 23px;
                            height: 35px;
                            padding: 0 10px;
                            &.noneVal {
                                color: #c4c4c4;
                                @include fontSize(15px,300);
                                line-height: 23px;
                            }
                            option {
                                color: #000;
                            }
                        }
                    }
                }
                .map-area {
                    height: 305px;
                    background: #ebebeb;
                    @include flexAllCenter;
                    &.fake-box {
                        background: url(../img/map.png) top center;
                        background-size: cover;
                        display: block;
                        position: relative;
                        .research-box {
                            @include flexCenter;
                            background: #666;
                            padding: 2px 10px;
                            position: absolute;
                            top: 13px;
                            left: 50%;
                            transform: translate(-50%,0);
                            z-index: 5;
                            p {
                                color: #fff;
                                @include fontSize(10px,100);
                            }
                            img {
                                margin-bottom: 2px;
                                width: 11px;
                            }
                        }
                        .pin-wrap {
                            position: absolute;
                            top: 0;
                            left: 0;
                            right: 0;
                            bottom: 0;
                            button {
                                @include btnNone;
                                position: relative;
                                img {
                                    position: absolute;
                                    bottom: 0;
                                    left: 50%;
                                    transform: translate(-50%,0);
                                }
                            }
                            .pin {
                                left: 20%;
                                top: 20%;
                                img {
                                    width: 16px;
                                }
                            }
                            .focus-pin {
                                left: 55%;
                                top: 40%;
                                img {
                                    width: 33px;
                                }
                            }
                        }
                    }
                    .etc-text {
                        p {
                            @include fontSize(12px,400);
                            color: #999;
                            line-height: 20px;
                            text-align: center;
                        }
                    }
                }
                .storeInfo-slide {
                    position: fixed;
                    bottom: 0;
                    left: 0;
                    width: 100%;
                    padding: 20px 20px 30px;
                    border-top: 1px solid #eee;
                    display: none;
                    .store-header {
                        position: relative;
                        margin-bottom: 5px;
                        span {
                            display: inline-block;
                            color: #fff;
                            @include fontSize(12px,300);
                            line-height: 18px;
                            background: #000;
                            padding: 3px 9px;
                            border-radius: 99px;
                            margin-right: 5px;
                        }
                        button {
                            position: absolute;
                            right: 0;
                            top: 50%;
                            transform: translate(0,-50%);
                            color: $mainColor;
                            border: 1px solid rgba($mainColor,.5);
                            background: none;
                            @include fontSize(11px,300);
                            padding: 1px 3px;
                        }
                    }
                    .store-address {
                        p {
                            @include fontSize(14px,400);
                            line-height: 20px;
                            width: 100%;
                            @include wordClear;
                        }
                        small {
                            margin-top: 5px;
                            @include fontSize(12px,400);
                            line-height: 14px;
                            color: #999;
                        }
                    }
                }
                .enjoyStore-list {
                    .none-list {
                        @include fontSize(12px,500);
                        color: #999;
                        text-align: center;
                        line-height: calc( 100vh - 146px );
                    }
                    ul {
                        li {
                            @include flexCenter;
                            padding: 20px 0;
                            border-bottom: 1px solid #eee;
                            &:first-child {
                                padding-top: 0;
                            }
                            .store-info {
                                width: calc( 100% - 28px );
                                margin-right: 20px;
                                .store-header {
                                    margin-bottom: 5px;
                                    span {
                                        display: inline-block;
                                        color: #fff;
                                        @include fontSize(12px,300);
                                        line-height: 18px;
                                        background: #000;
                                        padding: 3px 9px;
                                        border-radius: 99px;
                                        margin-right: 5px;
                                    }
                                }
                                .store-address {
                                    p {
                                        @include fontSize(14px,400);
                                        line-height: 22px;
                                    }
                                    small {
                                        margin-top: 5px;
                                        @include fontSize(12px,400);
                                        line-height: 14px;
                                        color: #999;
                                    }
                                }
                            }
                            >img {
                                width: 8px;
                            }
                        }
                    }
                }
            }
        }
    }
    .list-section {
        padding: 0 15px;
        >p {
            &:after {
                @include clearBoth;
            }
            margin-bottom: 5px;
            .alarmWarning {
                float: left;
                @include fontSize(10px,300);
                line-height: 15px;
                color: #999;
            }
            button {
                float: right;
                @include btnNone;
                @include fontSize(11px,100);
                border-bottom: .5px solid rgba(#999,.2);
                color: #999;
            }
        }
        >ul >li {
            position: relative;
            padding: 20px 0;
            border-bottom: 1px solid #eee;
            &:first-child {
                border-top: 1px solid #eee;
            }
            .delete-list {
                position: absolute;
                top: 5px;
                right: -15px;
                width: 40px;
                height: 40px;
                @include btnNone;
                @include flexAllCenter;
                img {
                    width: 10px;
                }
            }
            .list-date {
                margin-bottom: 5px;
                p {
                    @include fontSize(12px,400);
                    color: #333;
                    line-height: 14px;
                }
            }
            .list-subject {
                margin-bottom: 5px;
                p {
                    @include fontSize(17px,700);
                    line-height: 25px;
                }
            }
            .list-content {
                p {
                    @include fontSize(12px,400);
                    color: #666;
                    line-height: 20px;
                }
            }
        }
        .none-list {
            @include fontSize(12px,500);
            color: #999;
            text-align: center;
            line-height: calc( 100vh - 64px );
            border-top: 1px solid #eee;
        }
    }
}