.content-body {  
    &.allPickup-content{
        padding-bottom: 70px;
        .subMenu-container {
            margin-top: 10px;
            &:after {
                @include clearBoth;
            }
            .select-box {
                float: right;
                position: relative;
                padding-right: 17px;
                &:after {
                    width: 10px;
                    height: 8px;
                    content: "";
                    position: absolute;
                    background: url(../img/icon_select01.png) center center no-repeat;
                    background-size: cover;
                    right: 0;           
                    top: 50%;                    
                    transform: translate(0,-50%);
                }
                .listUp-btn {
                    border: 0;
                    height: 18px;
                    @include fontSize(12px,500);
                }
            }
        }
        .pickupList-container {    
            margin-top: 5px;
            border-top: 1px solid #eee;
            padding-top: 15px;
            .machine-tabWrapper {
                margin-bottom: 16px;
                .machine-tab {
                    &:after {
                        @include clearBoth;
                    }
                    li {
                        margin-top: 5px;
                        float: left;
                        padding: 7px 8px;
                        @include fontSize(12px,400);
                        margin-right: 5px;
                        color: #c4c4c4;
                        line-height: 14px;
                        &:last-child {
                            margin-right: 0;
                        }
                        &.active {
                            background: #000;
                            color: #fff;
                            @include fontSize(12px,500);
                        }
                    }
                }
            }
            .machine-tabItem {
                .machine-list {
                    li {
                        display: none;
                        &:after {
                            @include clearBoth;
                        }
                        &.active {
                            display: block;
                        }
                        .machine-item {
                            float: left;
                            margin-right: 10px;
                            margin-bottom: 20px;
                            width: calc( 50% - 5px );
                            &:nth-child(2n) {
                                margin-right: 0;
                            }
                            .img-box {
                                position: relative;
                                background: url(../img/image_02.png) center center;
                                background-size: cover;
                                margin-bottom: 9px;
                                .cart-ico {
                                    position: absolute;
                                    right: 0;
                                    bottom: 0;
                                    width: 25px;
                                }
                                .outOfStock {
                                    position: absolute;
                                    bottom: 0;
                                    background: rgba(0,0,0,.8);
                                    height: 25px;
                                    @include flexAllCenter;
                                    width: 100%;
                                    p {
                                        @include fontSize(12px,400);
                                        line-height: 18px;
                                        color: #fff;
                                    }
                                }
                            }
                            .price-box {
                                @include flexCenter;
                                margin-bottom: 3px;
                                height: 18px;
                                .percent {
                                    @include fontSize(15px,700);
                                    color: $mainColor;
                                    line-height: 18px;
                                    margin-right: 5px;
                                }
                                p {
                                    @include fontSize(15px,700);
                                    color: #333;
                                }
                                small {
                                    margin-top: 3px;
                                    @include fontSize(10px,300);
                                    margin-left: 2px;
                                    text-decoration-line: line-through;
                                    color: #333;
                                }
                            }
                            .item-subject {
                                p {
                                    @include fontSize(12px,400);
                                    color: #333;
                                    line-height: 18px;
                                    @include wordClear;
                                    width: 100%;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

