.content-body {
    &.listUp-detailContent {
        .listUp-container {
            .listUp-header {
                padding: 10px 0 20px;
                border-bottom: 1px solid #eee;
                .listUp-title {
                    @include fontSize(17px,500);
                    line-height: 22px;
                    margin-bottom: 5px;
                }
                .listUp-date {
                    @include fontSize(12px,400);
                    color: #999;
                    line-height: 14px;
                }
            }
            .listUp-text {
                padding: 20px 5px 0;
                pre {
                    @include fontSize(14px,400);
                    color: #333;
                    line-height: 20px;
                }
            }
        }
    }
}